import clsx from 'clsx';
import React from 'react'

interface RoundedIconProps {
    src: string;
    alt: string;
    className?: string;
    iconSize?: number;
}

export const RoundedIcon: React.FC<RoundedIconProps> = ({
    src,
    alt,
    className = "bg-white",
}) => {
    return (
        <div
            className={clsx(
                "w-12 h-12 p-2 rounded-full flex justify-center items-center overflow-hidden shrink-0",
                className
            )}
        >
            <img 
                src={src} 
                alt={alt} 
                className="w-full h-full object-contain"
            />
        </div>
    )
}

